/* GLOBAL */
:root {
  --dark_blue: #071731;
  --cream: #F7F3E2;
  --blue: #041D62;
  --yellow: #f1dc19;
  --green: #76d45f;
  --error: #f77047;
}

html { overflow-y: scroll; }

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Dongle', sans-serif;
  text-align: center;
  background-color: var(--cream);
  color: var(--blue);
  font-size: 2.5rem;
  letter-spacing: 0.1rem;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h2 {
  margin: 50px 0;
  line-height: 1;
}

.topIndexTitle{
  margin: 50px 0 0px 0;
}

h3{
  margin: 75px 0 10px 0;
  line-height: 1;
}

a {
  cursor: pointer;
  text-decoration: none;
	color: white;
}

a:hover{
  color: var(--green);
}

p {
  font-size: 1.5rem;
  margin:0;
}

.nav{
  background-color: var(--dark_blue);
  color: white !important;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.navItem {
  padding: 0 50px;
  display: flex;
}

.link {
  padding: 10px;
}

.linkBody {
  color: var(--green);
  font-size: 1.5rem;
}

.linkBody:hover {
    color: #6de24f;
    text-decoration: underline;
}

form {
  display: flex;
  align-items: center;
  flex-direction: column;
}

input{
  display: block;
  padding: 10px 20px;
  margin: 10px 0;
  border:none;
  border-bottom: 1px solid var(--cream);
  text-align: center;
  width: 30%;
  font-family: inherit;
  font-size: 1.5rem;
}

input:focus {
  border-bottom: 1px solid var(--green);
  outline: none !important;
}

.logo {
  width: 40px;
  transform: rotate(40deg);
}

.logo:hover{
  transform: rotate(90deg);
}

button{
  color: white;
  background-color: var(--blue);
  width: 100px;
  height: 50px;
	border:1px solid white;
	outline: none;
	/* border-radius: .25em; */
  text-transform: uppercase;
  font-weight: 700;
  cursor: pointer;
}

button:hover{
  background-color: white;
  color: var(--blue);
	border:1px solid var(--blue);
}

.buttonForm {
  margin: 40px 0;
}

.buttonVoteDiv{
  margin: 10px 0;
}

.itemCompareGlobal {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content:center;
  align-items: center;
}


.item{
  /* width: 20%; */
  background-color: white;
  border-radius: .555556rem;
  margin: 20px;
  padding: 30px 10px 10px 10px;
}

.item p {
  margin-bottom: 15px;
}

.addItem {
  display: flex;
  flex-direction: column;
  align-items: center;
}

textarea {
  background-color: var(--cream);
  outline: none;
  border:none;
  overflow-x: hidden;
  width: 50%;
  text-align: center;
  font-size: inherit;
  font-family: inherit;
  color: inherit;
  margin-top: 10px;
}

.error{
  font-size: 1rem;
  color: var(--error)
}

.giphy{
  font-size: 0.8rem;
  margin: 20px 0 10px 0;
}
.giphy a {
  color: var(--blue) !important;
}


.hallFame{
  margin-bottom: 75px;
}

.hallFameItem{
  display: flex;
  justify-content: center;
  align-items: center;
}

.hallFameItemDiv{
  margin: 0 10px;
}


select {
  text-align: center;
  width: auto;
height: 2.3125rem;
padding: .4rem 1rem .4rem 1rem;
border: 1px solid var(--cream);
margin: 0 0 1rem;
font-family: inherit;
font-size: 1.1rem;
background-color: #fff;
-webkit-appearance: none;
-moz-appearance: none;
background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" version="1.1" width="32" height="24" viewBox="0 0 32 24"><polygon points="0,0 32,0 16,24" style="fill: rgb%28138, 138, 138%29"></polygon></svg>');
background-size: 20px 6px;
background-position: 110%;
background-origin: content-box;
background-repeat: no-repeat;
height: auto;
}

@media (max-width: 900px) {
  input {
      width: 75%;
  }

  .itemCompareGlobal {
    display: flex;
    flex-direction: column;
  }

  .navItem {
    padding: 0 20px 0 20px;
    display: flex;
  }
  .item{
    /* width: 20%; */
    background-color: white;
    border-radius: .555556rem;
    margin: 0px;
    padding: 30px 0 0 0;
  }

  .hallFameItem{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }

  
}